import React, { Component } from 'react';
export default class Header extends Component {
    render() {

        let resumeData = this.props.resumeData;

        return (
            <React.Fragment>
                <header id="home">
                    <nav id="nav-wrap">
                        <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
                        <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
                        <ul id="nav" className="nav">
                            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
                            <li><a className="smoothscroll" href="#about">About</a></li>
                            <li><a className="smoothscroll" href="#resume">Resume</a></li>
                            <li><a className="smoothscroll" href="#portfolio">Portfolio</a></li>
                            <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
                            <li><a className="smoothscroll" href="#contact">Contact</a></li>
                        </ul> {/* end #nav */}
                    </nav> {/* end #nav-wrap */}
                    <div className="row banner">
                        <div className="banner-text">
                            <h1 className="responsive-headline">Meet {resumeData.name}</h1>
                            <h3>
                            A full Stack software engineering professional based in Columbus, OH with broad experience developing, enhancing, 
                            and supporting software implementations on enterprise initiatives. 
                            <br/> 
                            Intrigued? Start scrolling to learn more...
                            </h3>
                            <ul className="social">
                                <li><a href="http://www.linkedin.com/in/michael-f-rieser" target="_blank"><i className="fa fa-linkedin" /></a></li>
                            </ul>
                        </div>
                    </div>
                    <p className="scrolldown">
                        <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
                    </p>
                </header>
            </React.Fragment>
        );
    }
}