import React, { Component } from 'react';
export default class About extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="about">
                    <div className="row">
                        <div className="three columns">
                            <img className="profile-pic" src="images/profilepic.jpg" alt="" />
                        </div>
                        <div className="nine columns main-col">
                            <h2>About Me</h2>
                            <p>
                            I'm a positive self-starter with proven experience refining extant codebases, bootstrapping robust automation frameworks, 
                            and crafting web and native applications in both individual and collaborative environments.
                            I have 7+ years of experience working as a software engineer on everything from geospatial productivity enhancing scripts
                            in Python and VB.NET for local government entities, to Tier 1 enterprise Banking applications written in Java, to 
                            hardy test automation suites built with Ruby, Cucumber, and RSpec, and beyond...  
                            I am passionate about learning new technologies, crafting exquisite web applications, precision pencil illustration, and
                            running (to name a few). 
                            </p>
                            <div className="row">
                                <div className="columns contact-details">
                                    <h2>Contact Details</h2>
                                    <p className="address">
                                        <span>Jonathan Doe</span><br />
                                        <span>1600 Amphitheatre Parkway<br />
                                            Mountain View, CA 94043 US
                                        </span><br />
                                        <span>(123)456-7890</span><br />
                                        <span>anyone@website.com</span>
                                    </p>
                                </div>
                                <div className="columns download">
                                    <p>
                                        <a href="#" className="button"><i className="fa fa-download" />Download Resume</a>
                                    </p>
                                </div>
                            </div> {/* end row */}
                        </div> {/* end .main-col */}
                    </div>
                </section>

            </React.Fragment>
        );
    }
}