import React, { Component } from 'react';
export default class Resume extends Component {
  render() {
    return (
      <React.Fragment>
        <section id="resume">
          {/* Education
        ----------------------------------------------- */}
          <div className="row education">
            <div className="three columns header-col">
              <h1><span>Education</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="row item">
                <div className="twelve columns">
                  <h3>Penn State University, World Campus</h3>
                  <p className="info">Master in Geographic Information Systems <span>•</span> <em className="date">In Progress</em></p>
                  <p>
                    Expanding knowledge of computer programming and Geospatial technologies and how they can be leveraged to solve complex issues in the real world.
                  </p>
                </div>
              </div> {/* item end */}
              <div className="row item">
                <div className="twelve columns">
                  <h3>The Ohio State University</h3>
                  <p className="info">B.S. in Analytical Cartography <span>•</span> <em className="date">August 2011</em></p>
                  <p>
                    Excelled in core cartography courses in addition to advanced electives including web mapping, statistics, and computer programming.
                  </p>
                </div>
              </div> {/* item end */}
            </div> {/* main-col end */}
          </div> {/* End Education */}
          {/* Work
        ----------------------------------------------- */}
          <div className="row work">
            <div className="three columns header-col">
              <h1><span>Work</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="row item">
                <div className="twelve columns">
                  <h3>Nationwide Insurance</h3>
                  <p className="info">Specialist, Test Automation Lead / Software Craftsman <span>•</span> <em className="date">2015 - Present</em></p>
                  <p>
                  Lead the test automation effort for Nationwide Retirement Plan’s Strategic Portfolio initiative,
                  ensuring the highest level of quality on cutting edge business solutions. Seamlessly plan, coordinate, and execute the
                  testing strategy between NW teams, third party actors, and internal business units to delight customers and enable
                  Nationwide to maintain their competitive advantage. Continually mentor and uplift, while also proliferating test
                  automation at the enterprise level by forming the training standards for Ruby and Cucumber. Ventured into the web
                  development arena, becoming adept with AngularJS and Angular while enabling myself to fully contribute across all
                  levels of the stack.
                  </p>
                  <p className="info">Sr. Developer, Test Automation<span>•</span> <em className="date">2015 - 2017</em></p>
                  <p>
                  Ensured a high level of quality on Tier 1 software implementations for Nationwide Bank's Day One Lending and
                  Day One Deposits initiatives. Utilized ATDD in an agile environment to ensure deliverables satisfied business
                  specifications and operated effectively on a variety of platforms. Employed Ruby, Cucumber, Gherkin, and RSpec to
                  create succinct automated tests to safeguard against regressions. Lead in the administration of a Jenkins regression
                  implementation and resolved issues pertaining to versioning, test data, and Ruby scripts. Continually learned new
                  technologies, refined extant practices, and uplifted my team to provide additional value to our business partners.
                  </p>
                </div>
              </div> {/* item end */}
              <div className="row item">
                <div className="twelve columns">
                  <h3>Montgomery County Environmental Services</h3>
                  <p className="info">GIS Technician II <span>•</span> <em className="date">2013 - 2015</em></p>
                  <p>
                  Significantly enhanced the efficacy of processes and fidelity of data in order to provide an accurate, up-to-date
                  representation of Montgomery County’s water/wastewater, network, and billing infrastructure. Bolstered stakeholder
                  returns through the employment of crucial application knowledge and development skills gained both within and
                  beyond the scope of GIS Bachelor’s and Master’s Courses, including Python, .NET, and the ArcObjects SDK.
                  </p>
                </div>
              </div> {/* item end */}
            </div> {/* main-col end */}
          </div> {/* End Work */}
          {/* Skills
        ----------------------------------------------- */}
          <div className="row skill">
            <div className="three columns header-col">
              <h1><span>Skills</span></h1>
            </div>
            <div className="nine columns main-col">
              <p>
                I'm continually seeking to deepen my knowledge of web development and test automation while also branching out
                into exciting new arenas such as machine learning and cloud development.
              </p>
              <div className="skills-flex-container">
                <div className="skills-list">
                  <h1>Languages</h1>
                  <ul>
                    <li>Ruby</li>
                    <li>JavaScript</li>
                    <li>HTML5/CSS3</li>
                    <li>PLSQL</li>
                    <li>Python</li>
                  </ul>
              </div>
              <div className="skills-list">
                  <h1>SDKs/Modules</h1>
                  <ul>
                    <li>Cucumber</li>
                    <li>RSpec</li>
                    <li>Selenium/CSS3</li>
                    <li>WATIR</li>
                    <li>Nokogiri</li>
                  </ul>
              </div>
              <div className="skills-list">
                  <h1>Server/Database</h1>
                  <ul>
                    <li>Oracle</li>
                    <li>PostgreSQL</li>
                    <li>Express</li>
                    <li>Node</li>
                    <li>MongoDB</li>
                  </ul>
              </div>
              <div className="skills-list">
                  <h1>Web</h1>
                  <ul>
                    <li>AngularJS</li>
                    <li>Angular</li>
                    <li>Rails</li>
                    <li>jQuery</li>
                  </ul>
              </div>
              <div className="skills-list">
                  <h1>Cloud</h1>
                  <ul>
                    <li>AWS</li>
                    <li>Google Fusion Tables</li>
                    <li>Google Drive</li>
                    <li>Heroku</li>
                  </ul>
              </div>
              <div className="skills-list">
                  <h1>Additional</h1>
                  <ul>
                    <li>GUI Design</li>
                    <li>UI/UX</li>
                    <li>Artistic Design</li>
                    <li>Git</li>
                  </ul>
              </div>
              </div>
              {/*
              <div className="bars">
                <ul className="skills">
                  <li><span className="bar-expand photoshop" /><em>Photoshop</em></li>
                  <li><span className="bar-expand illustrator" /><em>Illustrator</em></li>
                  <li><span className="bar-expand wordpress" /><em>Wordpress</em></li>
                  <li><span className="bar-expand css" /><em>CSS</em></li>
                  <li><span className="bar-expand html5" /><em>HTML5</em></li>
                  <li><span className="bar-expand jquery" /><em>jQuery</em></li>
                </ul>
              </div>{/* end skill-bars */}
            </div> {/* main-col end */}
          </div> {/* End skills */}
        </section>
      </React.Fragment>
    );
  }
}